.coupleCard{padding:12px;border-radius:5px;background:#fff;box-shadow:0 1px 3px rgba(0,0,0,.1);margin-bottom:8px;border-left:5px solid #ced4da}.coupleHeader{display:flex;justify-content:space-between;margin-bottom:8px;font-size:13px}.coupleType{font-weight:600}.coupleNumber{color:#6c757d}.coupleName{font-weight:500;margin-bottom:8px;font-size:15px;line-height:1.3}.coupleDetails{display:flex;justify-content:space-between;font-size:13px;color:#6c757d}.лекция{border-left-color:#fa0}.лекция .coupleType{color:#fa0}.практика{border-left-color:#00cc73}.практика .coupleType{color:#00cc73}@media(max-width: 576px){.coupleCard{padding:10px}.coupleName{font-size:14px}}